import sweeperCalc from "@/services/sweepercalc.js";

const SET_BATTLE_RESULTS = function(state, payload){
    const battle = sweeperCalc(payload.weak, payload.pokemon)
    state.battleResults = battle;
    state.selectedAllyPokemon = Object.keys(battle.team)[0];
    state.battleSize = Object.keys(battle.team[state.selectedAllyPokemon]).length;
}

const SET_SELECTED_ALLY_POKEMON = function(state, pokemon){
    state.selectedAllyPokemon = pokemon
}

export default {
    SET_BATTLE_RESULTS,
    SET_SELECTED_ALLY_POKEMON
}