import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    name: "home",
    component: () => import(/* webpackChunkName: "about" */ '../views/Home.vue')
  },
  {
    path: '/explore',
    name: 'explore',
    component: () => import(/* webpackChunkName: "about" */ '../views/Explore.vue')
  },
  {
    path:"/dex/:tier",
    name:"tier",
    component:() => import('../views/Tier.vue'),
  },
  {
    path:"/pokemon/:tier/:pokemon",
    name:"pokemon",
    component:() => import('../views/Pokemon.vue')
  },
  {
    path: "/mysquad",
    name: "mysquad",
    component: () => import("../views/BreakMyTeam.vue")
  },
  {
    path: "/mysquad/shared/:tier/:id",
    name: "sharemyteam",
    component: () => import("../views/ShareMyTeam.vue")
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
