export default function(spreads){
    const syllable = ['hp', 'atk', 'def', 'spa', 'spd', 'spe']
    let res = {}

    for(const spread in spreads){
        const stats = spreads[spread][0].stats

        for(const stat in stats){
            const statLabel = syllable[stat]
            const statValue = Number(stats[stat])

            if (statLabel in res){
                if (res[statLabel].min >= statValue) res[statLabel].min = statValue
                if (res[statLabel].max <= statValue) res[statLabel].max = statValue
            } else {
                res[statLabel] = {min: statValue, max: statValue}
            }
        }
    }

    return res
}