import typeChart from "./typeChart"

function conculateStatAttack(category, attacker) {
    let baseOffense = 0
    let max = 0
    let stat = ''
    if (category == 'physical') {
        const atk = 1
        max = attacker.parsedSpreads['atk'].max
        baseOffense = Math.floor((Math.floor(((2 * attacker.stats[atk] + 31 + Math.floor(max / 4)) * attacker.level) / 100) + 5) * 1)
        stat = "atk"
    } else {
        const atk = 3
        max = attacker.parsedSpreads['spa'].max
        baseOffense = Math.floor((Math.floor(((2 * attacker.stats[atk] + 31 + Math.floor(max / 4)) * attacker.level) / 100) + 5) * 1)
        stat = "spa"
    }


    return {
        base: baseOffense,
        evs: `${max}${stat}`
    }
}

function calcFoeDefense(category, target) {
    let baseDefense = 0

    if (category == 'physical') {
        baseDefense = Math.floor((Math.floor(((2 * target.data.stats[2] + 31 + Math.floor(target.spread[2] / 4)) * target.level) / 100) + 5) * 1)
    } else {
        baseDefense = Math.floor((Math.floor(((2 * target.data.stats[4] + 31 + Math.floor(target.spread[4] / 4)) * target.level) / 100) + 5) * 1)
    }
    
    return baseDefense
}

function calculateHP(foe) {
    return Math.floor(((2 * foe.data.stats[0] + 31 + Math.floor(foe.spread[0] / 4)) * foe.level) / 100) + foe.level + 10
}

function calculateMods(foe, attacker, move) {
    let mod = 1
    if (attacker.types.includes(move.type)) {
        mod *= 1.5
    }

    for(const key in foe.data.types){
        
        const foe_typing = foe.data.types[key]

        if (move.type in typeChart[foe_typing].weakTo){
            mod *= typeChart[foe_typing].weakTo[move.type]
        }
        else if (move.type in typeChart[foe_typing].resists){
            mod *= typeChart[foe_typing].resists[move.type]
        }
    }

    return mod
}

export default function (move, foe, attacker) {
    const offense = conculateStatAttack(move.category, attacker)
    const D = calcFoeDefense(move.category, foe)
    const HP = calculateHP(foe)
    const mod = calculateMods(foe, attacker, move)

    let base = Math.floor(Math.floor(Math.floor(2 * attacker.level / 5 + 2) * move.power * offense.base / D) / 50) + 2
    base *= mod
    
    return  {
        aggressor: attacker.slug,
        victim: foe.data.slug,
        damage: (base / HP) * 100,
        move: move.name,
        evs: offense.evs
    }

}